.mts__features-container__feature {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-direction: row;

  margin: 1rem;
}

.mts__features-container__feature-title {
  flex: 1;
  max-width: 250px;
  margin-right: 2rem;
}

.mts__features-container__feature-title img {
  width: 100px;
  height: 80px;
  margin-bottom: 1rem;

  transition: all 1s ease-in-out;
}
.mts__features-container__feature-title img:hover {
  transform: scale(1.3);
}

.mts__features-container__feature-title h1 {
  font-family: var(--font-family);
  font-weight: 800;
  font-size: 18px;
  line-height: 24px;
  /* letter-spacing: -0.04rem; */
  color: #ffffff;
}

.mts__features-container__feature-title div {
  width: 38px;
  height: 3px;
  background: var(--gradient-bar);

  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.2);
  margin-bottom: 0.25rem;
}

.mts__features-container_feature-text {
  flex: 2;
  max-width: 390px;
  display: flex;
}

.mts__features-container_feature-text p {
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 14px;
  line-height: 24px;

  color: var(--color-text);
}

@media screen and (max-width: 550px) {
  .mts__features-container__feature-title h1 {
    font-size: 14px;
    line-height: 22px;
  }
  .mts__features-container__feature-title p {
    font-size: 12px;
    line-height: 20px;
  }
  .mts__features-container__feature {
    margin: 1rem 0;
  }
}
